
















import { defineComponent, toRefs } from '@vue/composition-api';
import useAppStore from '@/stores/app';

export default defineComponent({
	props: {
		to: {
			type: String,
			default: null,
		},
		icon: {
			type: String,
			default: 'box',
		},
		active: {
			type: Boolean,
			default: false,
		},
	},
	setup() {
		const appStore = useAppStore();
		const { drawerOpen } = toRefs(appStore.state);

		return { drawerOpen };
	},
});
