

















import { defineComponent } from '@vue/composition-api';
import useNotificationsStore from '@/stores/notifications';

export default defineComponent({
	props: {
		id: {
			type: String,
			required: true,
		},
		title: {
			type: String,
			required: true,
		},
		text: {
			type: String,
			default: null,
		},
		icon: {
			type: String,
			default: null,
		},
		type: {
			type: String,
			default: 'info',
			validator: (val: string) => ['info', 'success', 'warning', 'error'].includes(val),
		},
		tail: {
			type: Boolean,
			default: false,
		},
		dense: {
			type: Boolean,
			default: false,
		},
		showClose: {
			type: Boolean,
			default: false,
		},
		loading: {
			type: Boolean,
			default: false,
		},
		progress: {
			type: Number,
			default: undefined,
		},
	},
	setup(props) {
		const notificationsStore = useNotificationsStore();

		return { close };

		function close() {
			if (props.showClose === true) {
				notificationsStore.remove(props.id);
			}
		}
	},
});
